/* eslint-disable-next-line no-restricted-globals */
// routing
// auth provider
// import { FirebaseProvider as AuthProvider } from 'contexts/FirebaseContext';
// import { AWSCognitoProvider as AuthProvider } from 'contexts/AWSCognitoContext';
import { JWTProvider as AuthProvider } from 'contexts/JWTContext';
import { TableProvider } from 'contexts/TableContext';
import { getToken, MessagePayload, onMessage } from 'firebase/messaging';
// import { getMessaging, } from 'firebase/messaging/sw';
import NavigationScroll from 'layout/NavigationScroll';
import { useEffect } from 'react';
import { ToastContainer } from 'react-toastify';
import Routes from 'routes';
import { messaging } from 'serviceWorker';
import ThemeCustomization from 'themes';
import Snackbar from 'ui-component/extended/Snackbar';
// project imports
import Locales from 'ui-component/Locales';
import RTLLayout from 'ui-component/RTLLayout';

// ==============================|| APP ||============================== //

const App = () => {
    useEffect(() => {
        getToken(messaging, { vapidKey: 'BF8U0r1YSF4m93pBjjrqgjFX4bdxoR_JkV4m5cOJsuKOW7eMzoBPsjg4pylElTvEtkcJSVNIJ8q-yB72si3om2Y' })
            .then((currentToken) => {
                if (currentToken) {
                    localStorage.setItem('deviceId', currentToken);
                } else {
                    console.log('No registration token available. Request permission to generate one.');
                }
            })
            .catch((err) => {
                console.log('An error occurred while retrieving token. ', err);
            });
        onMessage(messaging, (payload: MessagePayload) => {
            navigator.serviceWorker
                .getRegistration('/firebase-cloud-messaging-push-scope')
                .then((registration: ServiceWorkerRegistration | any) => {
                    registration.showNotification(payload.notification?.title, {
                        ...payload.notification
                    });
                });
        });
    }, [messaging]);

    return (
        <ThemeCustomization>
            {/* RTL layout */}
            <RTLLayout>
                <Locales>
                    <NavigationScroll>
                        <AuthProvider>
                            <TableProvider>
                                <>
                                    <Routes />
                                    <Snackbar />
                                    <ToastContainer />
                                </>
                            </TableProvider>
                        </AuthProvider>
                    </NavigationScroll>
                </Locales>
            </RTLLayout>
        </ThemeCustomization>
    );
};

export default App;
